import React from "react";
import { NavLink } from "react-router-dom";
import "./styles.css";
function Homes() {
  return (
    <>
      <div style={{ display: "flex", gap: "20px", backgroundColor: "biscuit" }}>
        <NavLink to="./products" style={{ color: "orange" }}>
          products
        </NavLink>

        <NavLink to="./signup" style={{ color: "orange" }}>
          SignUp
        </NavLink>
        <NavLink to="./login" style={{ color: "orange" }}>
          login
        </NavLink>
      </div>
    </>
  );
}
export default Homes;
