import React from "react";
import { useNavigate } from "react-router-dom";
import { useAuth } from "./Auths";

const LOgin = () => {
  const navigate = useNavigate();
  const auth = useAuth();
  const handleout = () => {
    auth.logout();
    navigate("/");
  };
  return (
    <>
      LOGIN
      {auth.user}
      <button onClick={handleout}>Logut</button>
    </>
  );
};

export default LOgin;
